.close-icon {
    content: '\2715';
    cursor: pointer;
    position: absolute;
    right: calc(22.8% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
}

@keyframes blowup {
    from {
        transform: scale(1) !important;
    }

    to {
        transform: scale(1);
    }
}

.blowup {
    animation-name: blowup !important;
}